import {
  Box,
  Button,
  ButtonGroup,
  Checkbox,
  Collapse,
  List,
  ListItem,
  Paper,
  TextField,
  Typography,
} from "@mui/material";
import { useEffect, useState } from "react";
import DeleteDialouge from "./dialouge";
import ConfettiAdd from "./confetti";
import { TransitionGroup } from "react-transition-group";

export default function TaskList({
  tasks,
  onChangeTask,
  onDeleteTask,
  onClearTask,
}) {
  const [text, setText] = useState("");
  const [errorMsg, setErrorMsg] = useState("");

  const [view, setView] = useState(null);
  const [taskFilter, setFilter] = useState(() => () => true);
  const [active, setActive] = useState(0);
  const [open, setOpen] = useState(false);
  const [hover, setHover] = useState(null);

  const handleOpen = () => setOpen(true) || setView(null);
  const handleClose = () => setOpen(false);

  // useEffect(() => {
  //   inputRef.current.focus();
  //   //inputRef.current.value = "";
  // }, [view]);

  function handleChange(e) {
    setText(e.target.value);
  }

  useEffect(() => {
    if (!text) setErrorMsg("Oops! Task cannot be empty.");
    else setErrorMsg("");
  }, [text]);

  const handleKeyDown = (event, index, task) => {
    if (event.key === "Enter") {
      if (!event.shiftKey) {
        event.preventDefault();
        handle_edit(index, task);
      }
    }
  };

  const handle_edit = (index, task) => {
    //console.log("test");
    if (view === index) {
      if (!text) return;
      setView(null);

      onChangeTask({
        id: task.id,
        text: text,
        done: task.done,
      });
    } else {
      setText(task.text);
      setView(index);
    }
  };

  function toggleTask(task) {
    if (!task.done) ConfettiAdd();
    onChangeTask({ ...task, done: !task.done }, true);
  }

  const filteredTask = tasks.filter(taskFilter);

  const taskshow = filteredTask.map((task, index) => (
    <Collapse key={task.id}>
      <ListItem
        sx={{
          display: "flex",
          py: 1,
          "&:hover": {
            "& .MuiPaper-root": {
              borderBottomRightRadius: 0,
              borderBottomLeftRadius: 0,
            },
          },
          flexDirection: "column",
          justifyContent: "start",
          alignItems: "start",
        }}
        key={task.id}
        disablePadding
      >
        <Paper
          sx={{
            width: 1,
            display: "flex",
            alignContent: "center",
            justifyContent: "space-between",
          }}
        >
          <Checkbox
            type="checkbox"
            checked={task.done}
            onChange={() => toggleTask(task)}
            color="success"
            sx={{ display: view === index ? "none" : "inline" }}
          />
          <TextField
            sx={{
              display: view !== index ? "none" : "inline-block",
            }}
            InputProps={{ sx: { borderRadius: 0 } }}
            inputRef={(input) => input && input.focus()}
            type="text"
            value={text}
            helperText={errorMsg}
            error={errorMsg}
            onChange={handleChange}
            onKeyDown={(event) => handleKeyDown(event, index, task)}
            fullWidth
            multiline
            //onBlur={() => setView(null)}
          />
          <Typography
            variant="body1"
            onMouseEnter={() => setHover(index)}
            onMouseLeave={() => setHover(null)}
            sx={{
              py: 1,
              pr: 1,
              //bgcolor: "red",
              width: 1,
              overflow: "hidden",
              textOverflow: "ellipsis",
              display: view === index ? "none" : "inline-block",
              textDecoration: task.done ? "line-through" : "none",
            }}
          >
            {task.text}
          </Typography>
        </Paper>
        <Collapse in={hover === index || view === index} sx={{ width: 1 }}>
          <ButtonGroup
            variant="contained"
            sx={{
              /* display:
                hover === index || view === index ? "inline-flex" : "none", */
              "& .MuiButton-root": {
                borderRadius: 0,
              },
            }}
            fullWidth
            onMouseEnter={() => setHover(index)}
            onMouseLeave={() => setHover(null)}
          >
            <Button
              style={{
                display: view !== index ? "none" : "inline-block",
              }}
              onClick={() => handle_edit(index, task)}
            >
              Save
            </Button>
            <Button
              style={{
                display: view !== index ? "none" : "inline-block",
              }}
              onClick={() => setView(null)}
            >
              Cancel
            </Button>
            <Button
              sx={{
                display: view === index ? "none" : "inline-block",
              }}
              onClick={() => handle_edit(index, task)}
              disabled={task.done}
            >
              Edit
            </Button>
            <Button
              sx={{
                display: view === index ? "none" : "inline-block",
              }}
              onClick={() => setView(null) || onDeleteTask(task.id)}
            >
              Delete
            </Button>
          </ButtonGroup>
        </Collapse>
      </ListItem>
    </Collapse>
  ));
  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignContent: "center",
        mt: 4,
      }}
    >
      <ButtonGroup
        sx={{ display: "flex", justifyContent: "center" }}
        variant="outlined"
        color="black"
      >
        <Button
          sx={{ bgcolor: 0 === active ? "secondary.main" : "transparent" }}
          onClick={() => {
            setActive(0);
            setFilter(() => () => true);
          }}
        >
          All
        </Button>
        <Button
          sx={{ bgcolor: 1 === active ? "secondary.main" : "transparent" }}
          onClick={() => {
            setActive(1);
            setFilter(() => (task) => task.done);
          }}
        >
          Completed
        </Button>
        <Button
          sx={{ bgcolor: 2 === active ? "secondary.main" : "transparent" }}
          onClick={() => {
            setActive(2);
            setFilter(() => (task) => !task.done);
          }}
        >
          Uncompleted
        </Button>
      </ButtonGroup>
      <List
        sx={{
          display: "flex",
          justifyContent: "center",
          flexDirection: "column",
        }}
      >
        <TransitionGroup>{taskshow}</TransitionGroup>
      </List>
      <Box sx={{ py: 2, display: !filteredTask.length ? "inline" : "none" }}>
        <Typography textAlign="center" variant="h1">
          ⚠️
        </Typography>
        <Typography textAlign="center" sx={{ pt: 2 }} variant="h5">
          No tasks available.
        </Typography>
      </Box>
      <Button
        sx={{ width: 120, display: filteredTask.length ? "inline" : "none" }}
        variant="contained"
        onClick={handleOpen}
        color="error"
      >
        Remove all
      </Button>
      <DeleteDialouge
        onClearTask={onClearTask}
        isOpen={open}
        handleClose={handleClose}
      />
    </Box>
  );
}
