export default function tasksReducer(tasks, action) {
  switch (action.type) {
    case "added": {
      //console.log("added");
      return [
        ...tasks,
        {
          id: action.id,
          text: action.text,
          done: false,
        },
      ];
    }
    case "deleted": {
      return tasks.filter((t) => t.id !== action.id);
    }

    case "changed": {
      //console.log("changed");
      return tasks.map((t) => {
        if (t.id === action.task.id) {
          return action.task;
        } else {
          return t;
        }
      });
    }
    case "cleared": {
      return [];
    }
    default: {
      throw console.error("Unknown action: " + action.type);
    }
  }
}
