import "./App.css";
import AddTodo from "./components/addTodo";
import tasksReducer from "./components/tasksReducer";
import TaskList from "./components/tasklist";
import { useEffect, useReducer, useRef } from "react";
import { Container, IconButton, Typography } from "@mui/material";
import { SnackbarProvider, enqueueSnackbar, closeSnackbar } from "notistack";
import CloseIcon from "@mui/icons-material/Close";

export default function App() {
  const [tasks, dispatch] = useReducer(tasksReducer, [], () => {
    const savedTasks = JSON.parse(localStorage.getItem("tasks"));
    return savedTasks ? savedTasks : initialTasks;
  });
  //const { enqueueSnackbar } = useSnackbar();

  const nextId = useRef(tasks.length ? tasks.at(-1).id + 1 : 0);
  useEffect(() => {
    localStorage.setItem("tasks", JSON.stringify(tasks));
  }, [tasks]);

  function handleNoti(message, variant) {
    enqueueSnackbar(message, {
      variant: variant,
      action: (key) => (
        <IconButton size="small" onClick={() => closeSnackbar(key)}>
          <CloseIcon color="white" fontSize="small" />
        </IconButton>
      ),
    });
  }

  function handleAddTask(text) {
    handleNoti("Task added successfully!", "success");
    dispatch({
      type: "added",
      id: nextId.current++,
      text: text,
    });
  }

  function handleChangeTask(task, checkbox = false) {
    if (!checkbox) handleNoti("Task updated successfully!", "info");
    dispatch({
      type: "changed",
      task: task,
    });
  }

  function handleDeleteTask(taskId) {
    handleNoti("Task has been removed!", "error");
    dispatch({
      type: "deleted",
      id: taskId,
    });
  }

  function handleClearTask() {
    dispatch({
      type: "cleared",
    });
  }

  return (
    <Container
      sx={{
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
        width: { lg: 0.6, md: 0.6, sm: 0.8 },
      }}
    >
      <SnackbarProvider
        autoHideDuration={3000}
        maxSnack={3}
        anchorOrigin={{ horizontal: "right", vertical: "top" }}
        style={{
          maxWidth: 300,
          justifySelf: "end",
        }}
      />

      <Typography
        variant="h3"
        sx={{
          textAlign: "center",
          py: 4,
          fontSize: { xs: 30, sm: 40, md: 50 },
        }}
      >
        What's your plan for today?
      </Typography>
      <AddTodo onAddTask={handleAddTask} />
      <TaskList
        tasks={tasks}
        onChangeTask={handleChangeTask}
        onDeleteTask={handleDeleteTask}
        onClearTask={handleClearTask}
      />
    </Container>
  );
}

const initialTasks = [
  { id: 0, text: "Go for a Walk", done: false },
  { id: 1, text: "write a blog", done: false },
];
