import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Slide,
} from "@mui/material";

import DeleteForeverOutlinedIcon from "@mui/icons-material/DeleteForeverOutlined";

/* const Transition = forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});
 */
const Transition = (props) => <Slide direction="up" {...props} />;

export default function DeleteDialouge({ onClearTask, isOpen, handleClose }) {
  function handleDeleteAll() {
    onClearTask();
    handleClose();
  }

  return (
    <Dialog
      slots={{ transition: Transition }}
      open={isOpen}
      keepMounted
      onClose={handleClose}
    >
      <Box
        component="div"
        sx={{
          bgcolor: "#d32f2f",
          width: 50,
          borderRadius: 50,
          aspectRatio: 1 / 1,
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          alignSelf: "center",
          mt: 3,
        }}
      >
        <DeleteForeverOutlinedIcon
          color="white"
          sx={{
            fontSize: 35,
          }}
        />
      </Box>
      <DialogTitle sx={{ fontWeight: "bold" }} textAlign="center">
        {"Confirm Delete"}
      </DialogTitle>
      <DialogContent sx={{ color: "gray" }}>
        Are you sure you want to remove all the tasks?
      </DialogContent>
      <DialogActions sx={{ display: "flex", justifyContent: "center", mb: 2 }}>
        <Button
          sx={{ width: 0.4 }}
          color="dark"
          variant="outlined"
          onClick={handleClose}
        >
          Cancel
        </Button>
        <Button
          sx={{ width: 0.4 }}
          variant="contained"
          onClick={handleDeleteAll}
          color="error"
        >
          Delete
        </Button>
      </DialogActions>
    </Dialog>
  );
}
